export default {
	
	data() {
		return {
			form:{
				userCode:'',
				mnemonicWord:'',
				newPassword:''
			},
			repeatPassword:'',
			
		};
	},
	computed:{
	   i18n() {  
	     return this.$i18nMsg()  
	   }
	},
	onLoad:function(options){
		// this.fetchUserDetail()
		this.phone = options.phone
		this.email = options.email
		uni.setNavigationBarTitle({
		            title:this.$i18nMsg().page.restore
		});
	},
	methods:{
		
	    
		
		async submit(){
			if (this.form.newPassword !== this.repeatPassword) { 
				uni.showToast({
					title: this.$i18nMsg().registerRestore.pwdDiff,
					icon: 'none'
				});
				return
			}
			uni.showLoading({
				title: '提交中',
				mask: true
			});
			const res = await this.$http.get('/user/change_password',{data: this.form})
			uni.hideLoading()
			console.log(res)
			uni.showToast({
				title: '導入成功',
				icon: 'none',
				duration: 1000
			});
			setTimeout(function() {
				uni.navigateBack({
					delta: 1
				});
			}, 1500);
			
			 
		},
		
	}
}
